import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { SharedService } from '../../../../shared/services/shared.service';
import { UserService } from '../../../.././api/services/user.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-unapproved',
  templateUrl: './unapproved.component.html',
  styleUrls: ['./unapproved.component.scss']
})
export class UnapprovedComponent implements OnInit {

  unitListData:any;
  logsData:any;

  ItemUserStartIndex:any = 0;
  ItemUserEndIndex:any;
  totalUserItems:any;
  itemUserLimit:any = 15;

  ItemLogStartIndex:any = 0;
  ItemLogEndIndex:any;
  totalLogItems:any;
  itemLogLimit:any;
  

  unitData:string = "";

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  isMobile:boolean = false;

  modalService:any;

  selectAllUnapprovedUser:boolean = false;

  isUserSelected:boolean = false;

  isLogs:boolean = false;

  isUserDataLoaded:boolean = false;

  isLogsDataLoaded:boolean = false;


  @ViewChild("viewUnapprovedUserRef", { static: false }) viewUnapprovedUserRef: TemplateRef<any>;

  constructor(
  private injector: Injector,
  public dialog: MatDialog,
  private userService:UserService, 
  private sharedService:SharedService,
  private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getUserIndexParams(event){
    this.ItemUserStartIndex = event.ItemUserStartIndex;
    this.ItemUserEndIndex = event.ItemUserEndIndex;
  }

  getLogIndexParams(event){
    this.ItemLogStartIndex = event.ItemLogStartIndex;
    this.ItemLogEndIndex = event.ItemLogEndIndex;
  }

  getBlockNo(data, index){
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.unitListData[index].blockNo = data[0].apartmentBlockNumber;
      return data[0].apartmentBlockNumber;
    }
    
  }

  getBlockUnitNo(data, index){
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.unitListData[index].unitNo = data[0].apartmentBlockUnitNumber;
      return data[0].apartmentBlockUnitNumber;
    }
    
  }

  getNotes(data){
     if(data === undefined || data.length == 0)
    return '';
    else 
    return data[0].notes;
  }

  getUserRole(role, index){
    if(role === undefined || role.length == 0){
      return '';
    }
    else {
      this.unitListData[index].roleName = role[0].roleName;
      return role[0].roleName;
    } 
    
  }

  getUserRoleInfo(role){
    if(role.userRole === undefined || role.userRole.length == 0){
      return '';
    }
    else {
      return role.userRole[0].roleName;
    } 
  }

  @HostListener('window:resize', ['$event'])
    onResize(event){

      if(event.target.innerWidth <= 991)
      this.isMobile = true;
      else
      this.isMobile = false;

  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  getAllUnapprovedUser(){

    if(this.selectAllUnapprovedUser){
      _.each(this.unitListData, (item)=>{
        item.checked = true;
      })
      this.isUserSelected = true;
    }
    else{
      _.each(this.unitListData, (item)=>{
        item.checked = false;
      })
      this.isUserSelected = false;
    }

  }

  getSelectedUnapprovedUser(unit) {
    var length=0;
    _.each(this.unitListData, (item)=>{
        if(item.checked){
          length++;
        }
     })
    if(length>0){
      this.isUserSelected = true;
    }
    else{
      this.isUserSelected = false;
    }

    if(length==0){
      this.selectAllUnapprovedUser = false;
    }

    if(length == this.unitListData.length){
      this.selectAllUnapprovedUser = true;
    }
    
  }

  approveUsers() {
    this.isUserDataLoaded = false;
    _.each(this.unitListData, (item, index)=>{
        if(item.checked){
          this.userService.getUserById(item.id).subscribe((res:any) => {
              var user = res[0];
              user.isApproved = true;
              this.userService.UpdateUser(user).subscribe((res:any) => {
                
                this.unitListData.splice(index, 1);
                this.totalUserItems = this.unitListData.length;
                this.isUserDataLoaded = true;

              })
          });
        }
     })
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  showUserInfo(index){

    var userId = this.unitListData[index].id;

    var blockData = this.unitListData[index].apartmentBlockUnit;

    var userRole = this.unitListData[index].userRole;

    this.userService.getUserById(userId).subscribe((res : any) => {

      var userData = res[0];

      if(blockData === undefined || blockData.length == 0){
        userData.blockNo = "Not Available";
        userData.unitNo = "";
      }
      else {
        userData.blockNo = blockData[0].apartmentBlockNumber;
        userData.unitNo = blockData[0].apartmentBlockUnitNumber;
      }

      if(userRole === undefined || userRole.length == 0){
        userData.roleName = "Not Available";
      }
      else {
        userData.roleName = userRole[0].roleName;
      }

      this.dialog.open(this.viewUnapprovedUserRef, {
        panelClass: 'material',
        disableClose: true,
        data: userData
      });


    });

    

  }



  showLogs() {

    this.isLogs = !this.isLogs;

    this.sharedService.getJsonData().subscribe((res : any) => {

      this.isLogsDataLoaded = true;

      this.logsData = res.logsData;
      this.totalLogItems = this.logsData.length;

      if(this.totalLogItems>20){
        this.ItemLogEndIndex = 20;
      }
      else {
        this.ItemLogEndIndex = this.totalLogItems;
      }
      this.itemLogLimit = this.ItemLogEndIndex;

      

    },
    error => {
      console.log(error);
    }
    );

    
  }

  ngOnInit() {

    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;

  	

    this.userService.getAllUnApprovedUsers().subscribe((res : any) => {

      var unitListData = res;

      //filter active true items
      this.unitListData = unitListData.filter(data => {
        return data.active
      })

      this.totalUserItems = this.unitListData.length;

      if(this.totalUserItems>this.itemUserLimit){
        this.ItemUserEndIndex = this.itemUserLimit;
      }
      else {
        this.ItemUserEndIndex = this.totalUserItems;
      }

      this.isUserDataLoaded = true;

    },
    error => {
      console.log(error);
    }
    );

    // delete user
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        var params = {
          userId: this.unitListData[index].id,
          updateUserId: parseInt(this.cookieService.get('userId'))
        }

        this.isUserDataLoaded = false;

        this.userService.deleteUserById(params).subscribe((res:any) => {

          this.unitListData.splice(index, 1);
          this.totalUserItems = this.unitListData.length;
          this.isUserDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });

  }

}
