import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-add-users',
  templateUrl: './add-users.component.html',
  styleUrls: ['./add-users.component.scss']
})
export class AddUsersComponent implements OnInit {

  
  residentArray:any;
  residentCount:number = 1;
  isEditUser:boolean = false;

  @ViewChild('addUnitForm', {static: false}) addUnitForm;

  constructor(
  	private router: Router,
    private route: ActivatedRoute
  ) { }


  
  addResident() {
    this.residentCount++;
    this.residentArray =  Array(this.residentCount).fill(0).map((x,i)=>i);
  }

  getResidentArray(event){
    this.residentArray = event;
    this.residentCount = this.residentArray.length;
  }

  isMobileView() {
    return window.innerWidth <= 576;
  }

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.isEditUser = true;
    }

    this.residentArray =  Array(this.residentCount).fill(0).map((x,i)=>i);

  	

  }

}
