import { Component, OnInit } from '@angular/core';

import { SharedService } from '../../../../shared/services/shared.service';

@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.scss']
})
export class ReportsComponent implements OnInit {
  
  unitReportData:any;

  selectedReports:any = [];

  constructor(
  	private sharedService:SharedService
  ) { }

  getSelectedReports(item){
  	if(item.checked){
  		this.selectedReports.push(item.name);
  	}
  	else{
  		this.selectedReports = this.selectedReports.filter(data => {
  			if(data != item.name)
  				return data;
  		})
  	}
  }

  isReportSelected(){
  	return this.selectedReports.length > 0 ? true : false;
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  ngOnInit() {

  	this.sharedService.getJsonData().subscribe((res : any) => {

  		this.unitReportData = res.unitReport;

  	},
    error => {
      console.log(error);
    }
    );

  }

}
