import { Component, OnInit, Input, OnChanges, SimpleChanges, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { UserService } from '../../../../api/services/user.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { VehicleService } from '../../../../api/services/vehicle.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-approved-user-details',
  templateUrl: './approved-user-details.component.html',
  styleUrls: ['./approved-user-details.component.scss']
})
export class ApprovedUserDetailsComponent implements OnInit {
  
  
  @Input() selectedTab:number;

  user:any;

  isSeperatePage:boolean = false;

  approvedUsersData:any;

  isUserDataLoaded:boolean = false;

  apartmentBlockUnitId:any;

  modalService:any;

  unit:any;
  vehicle:any;

  vehicleTypeData:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private injector: Injector,
    public dialog: MatDialog,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  isTabSelected(){
  	return this.selectedTab != -1 ? 'show' : '';
  }

  isNotSlide() {
    return this.isSeperatePage ? 'full' : '';
  }

  getRoleName(role){
    if(role === undefined || role.length == 0){
      return '';
    }
    else {
      this.user.roleName = role[0].roleName;
      return role[0].roleName;
    }
  }

  getunitStatus(data){
    return data ? 'Yes' : 'No';
  }

  getVehicleType(id){

    _.each(this.vehicleTypeData, (item, index) => {

      if(item.lookupValueId == id){

        return item.lookupValueName;
      }

    })

  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }


  getUserDetails(userId){

    this.userService.getAllApprovedUsers().subscribe((res : any) => {

      var approvedUsersData = res.filter( item => {
        if(item.id == userId)
          return item;
      });

      this.user = approvedUsersData[0];

      this.apartmentService.getApartmentBlockUnitByUserId(userId).subscribe((res : any) => {

        this.unit = res[0] || {};
        this.apartmentBlockUnitId = this.unit.apartmentBlockUnitId;

        this.vehicleService.getVehicleByVehicleId(this.apartmentBlockUnitId).subscribe((res : any) => {

          this.vehicle = res[0] || {};
          this.isUserDataLoaded = true;

        });

        

      });

    },
    error => {
      console.log(error);
    }
    );

  }


  ngOnInit() {

    this.user = {};
    this.unit = {};
    this.vehicle = {};

    if(this.route.params['value'].id != undefined){
      this.isSeperatePage = true;
      this.getUserDetails(this.route.params['value'].id);
    }

    // delete user
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        var params = {
          userId: index,
          updateUserId: parseInt(this.cookieService.get('userId'))
        }

        this.isUserDataLoaded = false;

        this.userService.deleteUserById(params).subscribe((res:any) => {

          
          this.isUserDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });


  }

  ngOnChanges(changes: SimpleChanges) {


    //user details
    if(this.selectedTab != -1){

      this.isUserDataLoaded = false;

      this.getUserDetails(this.selectedTab);

    }

    this.lookupService.getLookupValueByLookupTypeId(2).subscribe((res:any) => {

      this.vehicleTypeData = res;

    },
    error => {
      
    }
    );
    

  }

}
