import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { UserService } from '../../../../../api/services/user.service';
import { LookupService } from '../../../../../api/services/lookup.service';
import { SharedService } from '../../../../../shared/services/shared.service';

import { ApartmentService } from '../../../../../api/services/apartment.service';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-add-resident',
  templateUrl: './add-resident.component.html',
  styleUrls: ['./add-resident.component.scss']
})
export class AddResidentComponent implements OnInit {
  
  
  blocksData:any;
  unitData:any;

  userType:string = null;
  genderType:string = null;

  apartmentBlockId:string = "";
  apartmentBlockUnitId:string = "";

  isEditUser:boolean = false;

  isBlockSelected:boolean = false;

  isUserSubmitted:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  user:any;

  @Input ('index') index;
  @Input ('array') residentArray;

  @Output() public outputParams = new EventEmitter();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
  ) { }


  isArrayOne(){
  	return this.residentArray.length > 1 ? true : false;
  }

  deleteResident(){
  	this.residentArray.splice(this.index, 1);
    this.outputParams.emit(this.residentArray);
  }

  getUnits() {
    this.apartmentService.getApartmentBlockUnitByBlockId(parseInt(this.apartmentBlockId)).subscribe((res:any) => {
        this.isBlockSelected = true;
        this.unitData = res;
        if(this.isEditUser){
          this.apartmentBlockUnitId = res[0].apartmentBlockUnitId.toString();
        }
    },
    error => {
      console.log(error);
    }
    );
  }


  submitAddResidentForm(form) {

    this.isUserSubmitted = true;
    this.isError = false;

    if(!this.isEditUser) {

      //add user
      let userDetails = {
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "firstName": this.user.firstName,
        "middleName": "string",
        "lastName": this.user.lastName,
        "emailId": this.user.emailId,
        "password": this.user.password,
        "salt": "string",
        "address1": "string",
        "address2": "string",
        "city": "string",
        "state": "string",
        "country": "string",
        "zipCode": "string",
        "genderId": parseInt(this.genderType),
        "dob": "2019-11-10T09:58:08.934Z",
        "joinedOn": "2019-11-10T09:58:08.934Z",
        "bloodGroup": "string",
        "phoneNumber": this.user.phoneNumber.toString(),
        "emergencyContactNumber": "string",
        "emergencyContactPerson": "string",
        "emergencyContactNumberSecondary": "string",
        "emergencyContactPersonSecondary": "string",
        "accessCardNumber": "string",
        "notes": "string",
        "profilePictureId": null,
        "isLiving": true,
        "isActive": true,
        "isApproved": false,
        "resetPassword": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "updatedBy": 0
      }

      //add user 
      this.userService.addUser(userDetails).subscribe((res:any) => {
            
            if(res.message) {

              var userId = res.message;

                let userRole = {
                  "userId": userId,
                  "roleId": parseInt(this.userType),
                  "isActive": true,
                  "insertedBy": parseInt(this.cookieService.get('userId')),
                  "insertedOn": "2019-11-10T10:00:28.212Z",
                  "updatedBy": 0,
                  "updatedOn": "2019-11-10T10:00:28.212Z"
                }

                this.userService.addUserRole(userRole).subscribe((res:any) => {

                  var apartmentBlockUnitUser = {
                    "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
                    "userId": userId,
                    "userTypeId": parseInt(this.userType),
                    "isActive": true,
                    "insertedBy": parseInt(this.cookieService.get('userId')),
                    "insertedOn": "2019-11-10T10:38:46.060Z",
                    "updatedBy": 0,
                    "updatedOn": "2019-11-10T10:38:46.060Z"
                  }

                  this.apartmentService.addApartmentBlockUnitUser(apartmentBlockUnitUser).subscribe((res:any) => {

                    this.isUserSubmitted = false;

                    if(res.message) {
                       this.sharedService.setAlertMessage("User added successfully");
                       this.router.navigate(['ams/unit&users/unapproved']);
                    }
                    else {
                        this.isError = true;
                        this.errorMessage = res.errorMessage;
                    }

                  },
                  error => {
                    console.log(error);
                  }
                  );

                },
                error => {
                  console.log(error);
                }
                );

            }
            else {

              this.isUserSubmitted = false;
              this.isError = true;
              this.errorMessage = res.errorMessage;

            }

      },
      error => {
        
      }
      );


    }

    else {

      //edit user
      var userDetails = {
        "userId": this.route.params['value'].id,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "firstName": this.user.firstName,
        "middleName": "string",
        "lastName": this.user.lastName,
        "emailId": this.user.emailId,
        "password": this.user.password,
        "salt": "string",
        "address1": "string",
        "address2": "string",
        "city": "string",
        "state": "string",
        "country": "string",
        "zipCode": "string",
        "genderId": parseInt(this.genderType),
        "dob": "2019-11-10T09:58:08.934Z",
        "joinedOn": "2019-11-10T09:58:08.934Z",
        "bloodGroup": "string",
        "phoneNumber": this.user.phoneNumber.toString(),
        "emergencyContactNumber": "string",
        "emergencyContactPerson": "string",
        "emergencyContactNumberSecondary": "string",
        "emergencyContactPersonSecondary": "string",
        "accessCardNumber": "string",
        "notes": "string",
        "profilePictureId": null,
        "isLiving": true,
        "isActive": true,
        "isApproved": this.user.isApproved,
        "resetPassword": true,
        "insertedBy": 0,
        "updatedBy": parseInt(this.cookieService.get('userId'))
      }


      //edit user 
      this.userService.UpdateUser(userDetails).subscribe((res:any) => {
            
            if(res.message) {

              var userId = res.message;

                var userRole = {
                  "userId": userId,
                  "roleId": parseInt(this.userType),
                  "isActive": true,
                  "insertedBy": parseInt(this.cookieService.get('userId')),
                  "insertedOn": "2019-11-10T10:00:28.212Z",
                  "updatedBy": 0,
                  "updatedOn": "2019-11-10T10:00:28.212Z"
                }

                this.userService.addUserRole(userRole).subscribe((res:any) => {

                  var apartmentBlockUnitUser = {
                    "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
                    "userId": userId,
                    "userTypeId": parseInt(this.userType),
                    "isActive": true,
                    "insertedBy": parseInt(this.cookieService.get('userId')),
                    "insertedOn": "2019-11-10T10:38:46.060Z",
                    "updatedBy": 0,
                    "updatedOn": "2019-11-10T10:38:46.060Z"
                  }

                  this.apartmentService.addApartmentBlockUnitUser(apartmentBlockUnitUser).subscribe((res:any) => {

                    this.isUserSubmitted = false;

                    if(res.message) {
                       this.sharedService.setAlertMessage("User updated successfully");
                       this.router.navigate(['ams/unit&users/unapproved']);
                    }
                    else {
                        this.isError = true;
                        this.errorMessage = res.errorMessage;
                    }

                  },
                  error => {
                    console.log(error);
                  }
                  );

                },
                error => {
                  console.log(error);
                }
                );

            }
            else {

              this.isUserSubmitted = false;
              this.isError = true;
              this.errorMessage = res.errorMessage;

            }

      },
      error => {
        
      }
      );


    }

  }

  ngOnInit() {

    this.user = {};

    //get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.blocksData = res;
      
    });


    if(this.route.params['value'].id != undefined) {
      
      this.isEditUser = true;  

      this.userService.getUserById(this.route.params['value'].id).subscribe((res:any) => {

        this.user = res[0];
        this.genderType = this.user.genderId.toString();

        

        // get role
        this.userService.getRolesByUserId(this.route.params['value'].id).subscribe((data:any) => {

          this.userType = data[0].roleId.toString();

        },
        error => {
          
        });

        //get block
        this.apartmentService.getApartmentBlockByUserId(this.route.params['value'].id).subscribe((block:any) => {
      

          this.apartmentBlockId = block[0].apartmentBlockId.toString();
          this.getUnits();

        },
        error => {
          
        });

      },
      error => {
        
      });

   }



  }

}
