import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from '../../../../api/services/user.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-approved',
  templateUrl: './approved.component.html',
  styleUrls: ['./approved.component.scss']
})
export class ApprovedComponent implements OnInit {

  totalUnits:any;
  totalUsers:number = 0;
  unitData:string = "";
  approvedUsersData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 5;

  selectedTab:number = -1;

  isUserDataLoaded:boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  showApprovedUserDetails(id) {



    if(!this.isMobileView()){
      if(this.selectedTab != id)
        this.selectedTab = id;
      else
        this.selectedTab = -1;
    }
    else {
      this.router.navigate(['ams/approved-user-details/'+id]);
    }
  	  
    console.log(this.selectedTab);

  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  isTabSelected(){
  	return this.selectedTab != -1 ? 'show' : '';
  }

  isMobileView() {
    return window.innerWidth <= 991;
  }

  getBlockNo(data, index){
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.approvedUsersData[index].blockNo = data[0].apartmentBlockNumber;
      return data[0].apartmentBlockNumber;
    }
    
  }

  getBlockUnitNo(data, index){
    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.approvedUsersData[index].unitNo = data[0].apartmentBlockUnitNumber;
      return data[0].apartmentBlockUnitNumber;
    }
    
  }

  isActive(num){
    return this.selectedTab == num ? 'active' : '';
  }

  ngOnInit() {

   

   this.userService.getAllApprovedUsers().subscribe((res : any) => {


      this.approvedUsersData = res.filter(item => {
        return item.userRole.length != 0 && item.apartmentBlockUnit.length != 0;
      });

      this.totalItems = this.approvedUsersData.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isUserDataLoaded = true;


    },
    error => {
      console.log(error);
    }
    );

    this.apartmentService.getApartmentBlockUnitByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res : any) => {


      this.totalUnits = res.length;

    },
    error => {
      console.log(error);
    }
    );

  }

}
